$darkerPurple: #b1b2ff;
$lighterPurple: #d2daff;
$lightestPurple: #eef1ff;
$blue: #aac4ff;

.main-nav {
  height: 50px;
  background-color: $darkerPurple;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  box-sizing: border-box;
}

.nav-item {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.nav-item > a {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.375rem;
  margin-right: 0.375rem;
  text-decoration: none;
  box-sizing: border-box;
  cursor: pointer;
}

#home-button > img {
  margin-left: 0.375rem;
  margin-right: 0.375rem;
  text-decoration: none;
  box-sizing: border-box;
  height: 100%;
  position: relative;
  width: 40px;
}

#home-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.375rem;
  margin-right: 0.375rem;
  text-decoration: none;
  box-sizing: border-box;
  cursor: pointer;
  padding-right: 5px;
  border-radius: 8px;
}

#home-button:hover {
  background-color: $lighterPurple;
}

#home-button > h1 {
  font-size: 2rem;
  position: relative;
  bottom: 5px;
  display: flex;
  align-self: flex-end;
  color: black;
  box-sizing: border-box;
}

.icon {
  transition: 0.7s ease-in-out;
}

.icon:hover {
  transform: rotate(360deg);
}

.fa-angellist {
  font-size: 30px;
}

.main-about-btn {
  cursor: pointer;
  margin-right: 0.375rem;
  font-size: 2rem;
  font-weight: 700px;
  border-radius: 8px;
  padding: 5px;
  box-sizing: border-box;
}

.main-about-btn:hover {
  background-color: $lighterPurple;
}
